import { Callbacks } from '@langchain/core/callbacks/manager';
import {
  BaseOutputParser,
  FormatInstructionsOptions,
  StructuredOutputParser,
} from '@langchain/core/output_parsers';

export const generateOutputParser = (
  outputParameters: unknown,
): BaseOutputParser => {
  if (!outputParameters || typeof outputParameters !== 'object') {
    class StrOutputParser extends BaseOutputParser<string> {
      override parse(text: string, callbacks?: Callbacks): Promise<string> {
        return Promise.resolve(text);
      }

      override lc_namespace: Array<string> = [
        'langchain',
        'output_parsers',
        'str',
      ];

      override getFormatInstructions(
        options?: FormatInstructionsOptions,
      ): string {
        return 'Return the output as a plain string.';
      }
    }

    return new StrOutputParser();
  }

  return StructuredOutputParser.fromNamesAndDescriptions(
    outputParameters as Record<string, string>,
  );
};
